
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
.ai-image-generator{
    width: 100%;
    height: 100vh;
    position: relative;
    padding: 0%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgba(191, 122, 122, 0.4);
    background: linear-gradient(to bottom, #8a8e5b, #0d2332);

}

  
.header{
    text-align: center;
    padding-bottom: 30px;
    
}
.header h1{
    
    color: rgb(175, 175, 189);
    padding-top: 0%;
    font-size: 80px;
}
.header h1 span{
    color: rgb(36, 30, 43);
  
    font-weight: 800;
    font-size: 80px;
   
    
}

.img-loading {
    display:flex;
    flex-direction: column ;


}
.img-loading img{

    width: 350px;
    border-radius: 50%;

}
.search-box{
    margin-top: 20px;
    display: flex;
    width: 900px;
    height: 65px;
    justify-content: space-around;
    align-items: center;
    border-radius: 50px;
    background: transparent;
    border: 0.1px solid grey;
    
} 
.search-input{
    width: 600px;
    height: 40px;
    background: transparent;
    border:none;
    outline: none;
    font-size: 18px;
    color: white;
    padding-left: 30px;
    margin-left: 30px;
    border-color: black;
}
.search-input::placeholder{
    color: #999;
}
.generate-media{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 265px;
    height: 60px;
    font-size: 20px;
    border-radius: 50px;
    background: rgb(151, 75, 223);
    cursor: pointer;
    color: white;
    background: linear-gradient(to bottom, #43192e, #1a081f);
}
.loading-bar{
    width: 0px;
    height: 8px;
    background: grey;

}
.loading-text{
    font-size: 18px;
    align-items: center;
    justify-content: center;
}
.loading-bar-full{
    width: 400px;
    height: 8px;
    background: #2a2729;
    transition: 50s;
}
.display-none{
    display: none;

} 